import { enableProdMode } from '@angular/core';
import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';

import { AppModule } from './app/app.module';
import { environment } from './environments/environment';

if (environment.production) {
  enableProdMode();
}

const /** @type {?} */ id = 'c5d03052-1716-4dfb-a08a-76967051d4cf';
const /** @type {?} */ title = 'Architect Address (Angular 4)';
const /** @type {?} */ width = {
  xs: 12,
  sm: 8,
  md: 6,
  lg: 5,
  xl: 4
};
const /** @type {?} */ initialHeight = '250px';
const /** @type {?} */ render = async function(container: HTMLElement) {
  return new Promise(async (resolve, reject) => {
    try {
      const /** @type {?} */ appRoot = document.createElement('architect-address-widget');
      container.appendChild(appRoot);
      await platformBrowserDynamic().bootstrapModule(AppModule);
      resolve();
    } catch ( /** @type {?} */error) {
      reject(error);
    }
  });
};

export default {
  id,
  title,
  width,
  initialHeight,
  render
}
